import WucTab from "@/components/wuc-tab/wuc-tab.vue";
import MescrollUni from "mescroll-uni/mescroll-uni.vue";
export default {
	components: {
		WucTab,
		MescrollUni
	},
	data() {
		return {
			tabList: [{name: ''}],
			TabCur: 0,
			idx: 1,
			infoList: [],
			page: 1,
			//下拉刷新配置
			isShowNoMore: true,
			mescroll: null,
			downOption: { 
				use: true, // 是否启用下拉刷新; 默认true
				auto: false, // 是否在初始化完毕之后自动执行下拉刷新的回调; 默认true
				textOutOffset: '释放更新' // 下拉的距离大于offset范围的提示文本
			},
			upOption: {
				use: true, // 是否启用上拉加载; 默认true
				auto: false, // 是否在初始化完毕之后自动执行上拉加载的回调; 默认true
				textLoading: "加载中...",
				textNoMore: "没有更多数据了",
				toTop: {
					src : null , 
					offset : 700 , 
					duration : 800 
				},
			},
			smallStyle: {
				width:'32%',
				margin:'auto 0.66%',
				height:window.innerWidth * 0.3 * 135 / 229 + 'px',
				'border-radius':'6px',
				'background-color': '#000000'
			},
			bigStyle:{
				width:'100%',
				height:window.innerWidth*135/229+'px',
				'border-radius':'6px'
			}
		};
	},
	onReachBottom() {
		this.mescroll && this.mescroll.onReachBottom();
	},
	// 必须注册列表滚动事件,使下拉刷新生效	
	onPageScroll(e) {
		this.mescroll && this.mescroll.onPageScroll(e);
	},
	methods: {
		mescrollInit(mescroll) {
			this.mescroll = mescroll;
		},
		downCallback(mescroll) {
			// 这里加载你想下拉刷新的数据, 比如刷新轮播数据
			// loadSwiper();
			// 下拉刷新的回调,默认重置上拉加载列表为第一页 (自动执行 mescroll.num=1, 再触发upCallback方法 )
			this.mescroll.resetUpScroll();
			this.page = 1;
			this.upDate();
		},
		tabChange(index) {
			this.infoList = [];
			this.idx = this.tabList[index].id;
			this.page = 1;
			this.upDate();
		},
		upCallback() {
			this.page ++;
			this.upDate();
		},
		upDate() {
			uni.request({
				url: "http://migu.zmr016.com/article/list",
				method: 'GET',
				data: {
					type: this.idx,
					page: this.page
				},
				success: res => {
					if (res.data.data != '') {
							this.infoList = this.infoList.concat(res.data.data);
							this.mescroll.endSuccess();
							console.log(this.infoList,1111);
					} else {
						this.mescroll.endErr();
						// this.mescroll.endUpScroll(isShowNoMore)
					}
				},
			});
		}
	},
	//页面初始化生命周期函数
	onLoad() {
		uni.request({
			url: 'http://migu.zmr016.com/article/types',
			method: 'GET',
			success: res => {
				this.tabList = res.data.data;
			},
		});
		this.upDate();
	},
	mounted() {
		// console.log(this.tabList,777);
		// console.log(this.infoList,888);
	},
	onReady() {}
};
